import {
  Container,
  Typography,
  Button,
  useMediaQuery,
  useTheme,
  Table,
  Paper,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from "@mui/material";
import React from "react";
import { Box } from "@mui/system";
import Diagram from "../../components/chart";
import { Link } from "react-router-dom";
import { makeStyles } from "@mui/styles";
import { useAuth } from "../../utils/isAuthenticated";
// import { useSelector } from "react-redux";
import { courses, MTableRow } from "../../shared/animations";
import { useSelector } from "react-redux";
// import { Pay24 } from "../../api";

const Courses = ({ t, currencies, mode }) => {
  const classes = useStyles();
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down(992));

  const isAuth = useAuth();
  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const currenciesContent = Object.values(
    typeof currencies === "object" && currencies
  );

  return (
    <Paper
      component="section"
      sx={{
        pt: 5,
        pb: 5,
      }}
    >
      <Container>
        <Table
          sx={{
            borderCollapse: "separate",
            borderSpacing: "0px 8px",
          }}
        >
          <TableHead>
            <TableRow
              sx={{
                "& th": {
                  border: "none",
                  fontSize: 20,
                  color: "#64748B",
                  fontWeight: 400,
                },
              }}
            >
              <TableCell>{t("coinTitle")}</TableCell>
              <TableCell align={md ? "right" : "left"}>{t("price")}</TableCell>
              {!md && (
                <>
                  <TableCell>{t("dynamic")}</TableCell>
                  <TableCell>{t("market")}</TableCell>
                </>
              )}
            </TableRow>
          </TableHead>
          <TableBody>
            {СurrenciesList
              .filter((item) => item.rate)
              .filter(
                (item) =>
                  item.currency === "BTC" ||
                  item.currency === "ETH" ||
                  item.currency === "LTC" ||
                  item.currency === "BCH" ||
                  item.currency === "SOL" ||
                  item.currency === "KGS"
              )
              .map((coin, idx) => (
                <MTableRow
                  custom={idx + 1}
                  variants={courses}
                  initial="hidden"
                  whileInView="visible"
                  viewport={{ once: true }}
                  elevation={1}
                  component={Paper}
                  sx={{
                    color: "#000",
                    background: mode === "light" && "#FFF",
                    boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
                    borderRadius: "16px",

                    "& td": { border: "none" },
                    "& td:first-of-type": {
                      borderTopLeftRadius: "16px",
                      borderBottomLeftRadius: "16px",
                    },
                    "& td:last-child": {
                      borderBottomRightRadius: "16px",
                      borderTopRightRadius: "16px",
                    },
                  }}
                  key={idx}
                  hover
                >
                  <TableCell>
                    <Link
                      to={
                        isAuth
                          ? `/dashboard/exchange/${coin.currency}`
                          : `/accounts/login?return_to=/dashboard/exchange/${coin.currency}`
                      }
                    >
                      <Box
                        display="flex"
                        flexDirection="row"
                        alignItems="center"
                      >
                        <img
                          style={{ objectFit: "contain" }}
                          width="40"
                          height="40"
                          src={coin.logo}
                          alt="coin"
                        />
                        <Box
                          ml={2}
                          flexDirection={{ xs: "column", md: "row" }}
                          alignItems="center"
                        >
                          <Typography
                            color="inherit"
                            component="span"
                            mr={{ xs: 0, md: 1 }}
                            fontWeight={600}
                            fontSize={{ xs: 23, md: 19 }}
                          >
                            {coin.currency === "KGS" && "USDT/"}
                            {coin?.currency}
                          </Typography>
                          <Typography variant="body2" color="#64748B">
                            {coin.name}
                          </Typography>
                        </Box>
                      </Box>
                    </Link>
                  </TableCell>
                  <TableCell align={md ? "right" : "left"}>
                    <Typography fontSize={{ xs: 23, md: 19 }} fontWeight={600}>
                      {coin?.currency !== "KGS" && "$"}
                      {coin?.rate}
                      {coin.type === "fiat" && coin?.name}
                      {coin?.currency === "KGS" && " сом"}
                    </Typography>
                  </TableCell>
                  {!md && (
                    <>
                      <TableCell>
                        {coin?.difference && (
                          <Typography
                            sx={{
                              width: "fit-content",
                              padding: "8px 12px",
                              borderRadius: 2,
                              background:
                                coin.difference && coin.difference[0] === "+"
                                  ? "#ECFDF5"
                                  : coin.difference &&
                                    coin.difference[0] === "-"
                                  ? "#FEF2F2"
                                  : "#F7F8F9",
                              color:
                                coin.difference && coin.difference[0] === "+"
                                  ? "#10B981"
                                  : coin.difference &&
                                    coin.difference[0] === "-"
                                  ? "#EF4444"
                                  : "#A0ABBB",
                            }}
                            fontWeight={600}
                          >
                            {coin?.difference === undefined
                              ? "0.00"
                              : coin?.difference}{" "}
                            {coin.currency !== "KGS" && "$"}
                          </Typography>
                        )}
                      </TableCell>
                      <TableCell>
                        <Box style={{ width: 100 }}>
                          <Diagram
                            height={37}
                            color={
                              coin.difference && coin.difference[0] === "+"
                                ? "#10B981"
                                : coin.difference && coin.difference[0] === "-"
                                ? "#F15A5A"
                                : "#C1C7D2"
                            }
                            rates={coin.rates}
                          />
                        </Box>
                      </TableCell>
                    </>
                  )}
                </MTableRow>
              ))}
          </TableBody>
        </Table>

        <Link
          to={
            isAuth
              ? "/dashboard/rates"
              : "/accounts/login?return_to=/dashboard/rates"
          }
        >
          <Button
            className={classes.courseButton}
            variant="contained"
            color="secondary"
            size="large"
          >
            {t("ViewAllCourses")}
          </Button>
        </Link>
      </Container>
    </Paper>
  );
};

export default Courses;

const useStyles = makeStyles((theme) => ({
  courseButton: {
    display: "block",
    margin: "24px auto 0 auto",
  },
}));
