import React from "react";
import clock from "../../assets/images/benefits/alarm.webp";
import startup from "../../assets/images/benefits/startup.webp";
import cup from "../../assets/images/benefits/cup.webp";
import shield from "../../assets/images/benefits/shield.webp";
import chatting from "../../assets/images/benefits/chatting.webp";
import { Box, Container, Grid, Typography } from "@mui/material";
import { courses, MGrid } from "../../shared/animations";

const Benefits = ({ t }) => {
  const benefitsArray = [
    { title: t("benefits1"), img: cup },
    { title: t("benefits2"), img: shield },
    { title: t("benefits3"), img: clock },
    { title: t("benefits4"), img: startup },
    { title: t("benefits5"), img: chatting },
  ];
  return (
    <Box backgroundColor="#000" pt={10} pb={10}>
      <Container maxWidth="lg">
        <Grid container justifyContent="center" alignItems="center" spacing={2}>
          {benefitsArray.map((item, idx) => (
            <MGrid
              custom={idx + 1}
              variants={courses}
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true }}
              item
              xs={6}
              key={idx}
              md={2.4}
            >
              <Box
                sx={{
                  background: "#2B2B2B",
                  borderRadius: 1,
                  transition: "all 0.2s linear",
                  padding: "21px 19px",
                  color: "#FFF",
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  justifyContent: "center",

                  "& img": {
                    height: 72,
                    transition: "all 0.2s linear",
                  },

                  "&:hover": {
                    boxShadow: "0 0 5px #FCE34D",

                    "& img": {
                      transform: "scale(1.1)",
                    },
                  },
                }}
              >
                <img src={item.img} alt="" />
                <Typography
                  textAlign="center"
                  variant="h5"
                  mt={2}
                  fontWeight={300}
                >
                  {item.title}
                </Typography>
              </Box>
            </MGrid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default Benefits;
