import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getMainList } from "../../redux/reducers/Main";
import Benefits from "./Benefits";
import BuyCrypto from "./BuyCrypto";
import Courses from "./Courses";
import FirstScreen from "./FirstScreen";
import "./index.css";
// import MobileApps from "./MobileApps";
import News from "./News";
import Statictic from "./Statictic";
import Partners from "./Partners";

const Home = ({ t, i18n, mode }) => {
  const dispatch = useDispatch();
  const { MainList } = useSelector(({ MainReducer }) => MainReducer);
  useEffect(() => {
    dispatch(getMainList());
    document.title =
      "Купить и продать криптовалюту в Бишкеке. Обмен Биткоина, USDT";
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <FirstScreen t={t} />
      <Courses mode={mode} t={t} currencies={MainList?.currencies} />
      <Benefits t={t} />
      <BuyCrypto t={t} mode={mode} />
      <Statictic t={t} statistics={MainList?.statistics} />
      {/* <MobileApps t={t} /> */}

      <Partners t={t} />
      {/* <Deals t={t} /> */}
      {/* <NetexKassa t={t} /> */}
      <News t={t} i18n={i18n} news={MainList?.news} />
    </>
  );
};

export default Home;
