import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Autoplay, EffectFade, Keyboard } from "swiper";
import { Box, useMediaQuery, useTheme } from "@mui/material";
import { MouseParallaxContainer } from "react-parallax-mouse";
import "../index.css";
import { First, Second } from "./Slides";

SwiperCore.use([Pagination, Autoplay, EffectFade, Keyboard]);

const FirstScreen = ({ t }) => {
  const theme = useTheme();
  const lg = useMediaQuery(theme.breakpoints.down(992));
  const xs = useMediaQuery(theme.breakpoints.down(576));

  return (
    <Box
      component="section"
      pt={{ xs: "110px", md: 9.5 }}
      pb={{ xs: 2.5, md: 3 }}
      className="firstScreen black-bg"
      sx={{
        "& .swiper-pagination-bullets": {
          display: "flex",
          justifyContent: "center",
          columnGap: 1,
          bottom: 42,
          mt: { xs: 3, md: 2 },
          mb: 4,
          "& .swiper-pagination-bullet": {
            width: 16,
            height: 16,
            background: "#2B2B2B",
            borderRadius: "100%",
            [theme.breakpoints.down(576)]: {
              width: 10,
              height: 10,
            },
          },
          "& .swiper-pagination-bullet-active": {
            background: "#787878",
          },
        },
      }}
    >
      <MouseParallaxContainer>
        <Swiper
          keyboard={{
            enabled: true,
          }}
          loop={true}
          grabCursor={true}
          cubeEffect={{
            shadow: true,
            slideShadows: true,
            shadowOffset: 20,
            shadowScale: 0.94,
          }}
          slidesPerView={1}
          spaceBetween={100}
          pagination={{ clickable: true }}
          mousewheel={true}
          autoplay={{
            delay: 3000,
            pauseOnMouseEnter: true,
            disableOnInteraction: false,
          }}
        >
          {/* <SwiperSlide>
            <Third t={t} xs={xs} lg={lg} />
          </SwiperSlide> */}
          <SwiperSlide>
            <Second t={t} xs={xs} lg={lg} />
          </SwiperSlide>
          <SwiperSlide>
            <First t={t} xs={xs} lg={lg} />
          </SwiperSlide>
        </Swiper>
      </MouseParallaxContainer>
    </Box>
  );
};

export default FirstScreen;
