import { Box,  Container, Paper, Typography } from "@mui/material";
import React from "react";
import Card from "../../components/NewsCard";
import { ButtonRoute } from "../../shared/Buttons";
import { courses, MBox } from "../../shared/animations";

const News = ({ t, i18n, news }) => {
  // const history = useHistory();

  const newsContent = Object.values(typeof news === "object" && news);

  return (
    <Box component={Paper} p="64px 0">
      <Container maxWidth="lg">
        <Typography variant="h2" mb={4}>
          {t("NewsTitle")}
        </Typography>
        {newsContent?.slice(0, 4).map((item, idx) => (
          <MBox
            custom={idx + 1}
            variants={courses}
            initial="hidden"
            whileInView="visible"
            key={idx}
            viewport={{ once: true }}
          >
            <Card item={item} i18n={i18n} />
          </MBox>
        ))}
        <center>
          <ButtonRoute
            variant="contained"
            size="large"
            path="/main/news"
            color="secondary"
            sx={{ mt: 6 }}
            txt={t("ReadNews")}
          />
        </center>
      </Container>
    </Box>
  );
};

export default News;
