/* eslint-disable eqeqeq */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import {
  Container,
  InputLabel,
  Select,
  Typography,
  useTheme,
  useMediaQuery,
  MenuItem,
  Box,
  Paper,
  FormControl,
  TextField,
} from "@mui/material";
import success from "../../assets/images/state/saccess-lg.webp";
import recycle from "../../assets/images/state/recycle.webp";
import dollar from "../../assets/images/state/dollarCoin.webp";
import visa from "../../assets/images/paymentsSystem/visa.svg";
import masterCard from "../../assets/images/paymentsSystem/masterCard.svg";
import { ButtonRoute } from "../../shared/Buttons";
import { useDispatch, useSelector } from "react-redux";
import CompareArrowsIcon from "@mui/icons-material/CompareArrows";
import ReviewsCount from "./ReviewsCount";
import { Grid } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { useFormik } from "formik";
import { MGrid, MTypography, text } from "../../shared/animations";
import { getСurrenciesList } from "../../redux/reducers/Сurrencies";

const BuyCrypto = ({ t, mode }) => {
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down(992));
  const classes = useStyles();
  const dispatch = useDispatch();

  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const formik = useFormik({
    initialValues: {
      amount: 1000,
      id: 2,
      currency: {
        currency: "BTC",
      },
      sum: 0,
    },
    onSubmit: () => {},
  });

  const values = formik?.values;

  useEffect(() => {
    dispatch(getСurrenciesList());
  }, []);

  useEffect(() => {
    if (Array.isArray(СurrenciesList)) {
      const btc = СurrenciesList.find((item) => item.id == 2);
      formik.setFieldValue("currency", btc);
      formik.setFieldValue("sum", values.amount / parseInt(btc?.rate));
    }
  }, [СurrenciesList]);

  useEffect(() => {
    formik.setFieldValue(
      "currency",
      СurrenciesList.find((item) => item.id === values.id)
    );
    formik.setFieldValue(
      "sum",
      values?.amount / parseInt(values?.currency?.rate)
    );
  }, [values.id]);

  useEffect(() => {
    formik.setFieldValue(
      "sum",
      values?.amount / parseInt(values?.currency?.rate)
    );
  }, [values.amount]);

  return (
    <Box
      component={Paper}
      pt={{ xs: 4, md: 8 }}
      pb={{ xs: 4, md: 10 }}
      className="buy-crypto"
      sx={{
        "& .MuiFormControl-root": {
          boxShadow: "none",
          "& fieldset": {
            borderColor: "#B8C0CC",
          },
        },
      }}
    >
      <Container maxWidth="lg">
        <Grid container justifyContent={{ xs: "center", lg: "space-between" }}>
          <Grid item xs={12} md={6}>
            <MTypography
              custom={1}
              variants={text}
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true }}
              variant="h2"
              textAlign={{ xs: "center", md: "start" }}
              mb={3}
              fontWeight={500}
            >
              {t("BuyCurrencies")}
            </MTypography>
            <MTypography
              custom={2}
              variants={text}
              initial="hidden"
              whileInView="visible"
              variant="h6"
              viewport={{ once: true }}
              color="secondary."
              textAlign={{ xs: "center", md: "start" }}
              fontWeight={400}
              maxWidth={{ xs: "unset", md: 614 }}
              borderBottom="1px solid #E7EAEE"
              mb={4}
              pb={4}
            >
              {t("BuyDescription")}
            </MTypography>
            <Box
              display="flex"
              flexDirection="column"
              alignItems="start"
              borderBottom="1px solid #E7EAEE"
              pb={4}
              mb={4}
            >
              <ReviewsCount buy t={t} />
              <ButtonRoute
                path="/main/reviews"
                variant="contained"
                sx={{
                  mt: { xs: 2, md: 3 },
                }}
                fullWidth={md && true}
                color="secondary"
                size="medium"
                txt={t("ReadReviews")}
              />
            </Box>
            <div className="roadmap">
              <div className="roadmap-img">
                <img src={dollar} alt="" />
              </div>
              <div className="roadmap-img">
                <img src={recycle} alt="" />
              </div>
              <div className="roadmap-img">
                <img src={success} alt="" />
              </div>
            </div>
          </Grid>
          <MGrid
            custom={2}
            variants={text}
            viewport={{ once: true }}
            initial="hidden"
            whileInView="visible"
            item
            sx={{ display: "flex", justifyContent: "flex-end" }}
            xs={12}
            md={6}
          >
            <Box
              component={Paper}
              elevation={mode === "light" ? 0 : 3}
              sx={{
                background: mode === "light" && "#FFF",
                width: { xs: "100%", md: "74%" },
                boxShadow: "0px 12px 24px rgba(0, 0, 0, 0.1)",
                borderRadius: 1,
                p: 4,
                transition: "all 0.4s linear",
              }}
            >
              <Typography mb={2} variant="body1">
                {t("Currency")}
              </Typography>
              <FormControl fullWidth>
                <InputLabel>{t("SelectCurrency")}</InputLabel>
                <Select
                  name="id"
                  label={t("SelectCurrency")}
                  variant="outlined"
                  value={formik.values.id}
                  onChange={formik.handleChange}
                >
                  {Array.isArray(СurrenciesList) &&
                    СurrenciesList
                      ?.filter((item) => item.currency !== "USDT")
                      ?.map((item, idx) => (
                        <MenuItem key={idx} value={item.id}>
                          <img
                            width="30px"
                            height="30px"
                            style={{ marginRight: 10 }}
                            src={item.logo}
                            alt=""
                          />
                          {item.name}
                        </MenuItem>
                      ))}
                </Select>
              </FormControl>
              <Typography variant="body1" mt={2} mb={2}>
                {t("USDT")}
              </Typography>
              <TextField
                fullWidth
                type="text"
                label={t("EnterAmount")}
                name="amount"
                value={formik.values.amount}
                onChange={formik.handleChange}
                variant="outlined"
                onKeyPress={(event) => {
                  if (!/[0-9.,]/.test(event.key)) {
                    event.preventDefault();
                  }
                }}
              />

              <Box mt={2.5}>
                <Typography variant="h6">{t("inTotal")}</Typography>
                <Box
                  display="flex"
                  alignItems="center"
                  justifyContent="space-between"
                  sx={{ flexDirection: { xs: "column", sm: "row" } }}
                >
                  <Box
                    display="flex"
                    alignItems="center"
                    sx={{ flexDirection: { xs: "column", sm: "row" } }}
                  >
                    <Typography whiteSpace="nowrap" variant="body2">
                      {t("atRate")} 1.00 {formik.values?.currency?.currency}
                    </Typography>
                    <Box p="0 10px">
                      <CompareArrowsIcon className={classes.icon} />
                    </Box>
                    <Typography whiteSpace="nowrap" variant="body2">
                      ${formik.values?.currency?.rate}
                    </Typography>
                  </Box>
                  <Box display="flex" whiteSpace="nowrap" alignItems="center">
                    ~ {formik?.values?.sum?.toFixed(values?.currency?.decimal)}{" "}
                    {formik.values?.currency?.currency}
                  </Box>
                </Box>
              </Box>
              <ButtonRoute
                path={`/dashboard/buy/details/${values?.currency?.currency}`}
                variant="contained"
                sx={{ mt: 2, mb: 2 }}
                fullWidth
                color="primary"
                size="large"
                txt={t("buy")}
              />
              <ButtonRoute
                variant="contained"
                color="secondary"
                path="/dashboard/replenish/fiat"
                fullWidth
                size="large"
                txt={t("BuyBankCard")}
              />
              <Box
                display="flex"
                justifyContent="center"
                flexDirection="column"
                alignItems="center"
                borderTop="1px solid #E7EAEE"
                pt={3}
                mt={3}
              >
                <Typography>{t("WeAccept")}</Typography>
                <div>
                  <img src={masterCard} style={{ marginRight: 16 }} alt="" />
                  <img src={visa} alt="" />
                </div>
              </Box>
            </Box>
          </MGrid>
        </Grid>
      </Container>
    </Box>
  );
};

export default BuyCrypto;

const useStyles = makeStyles((theme) => ({
  icon: {
    [theme.breakpoints.down(768)]: {
      transform: "rotate(90deg)",
    },
  },
}));
