import React from "react";
import cup from "../../assets/images/buyCrypto/cup-black.svg";
import cupWhite from "../../assets/images/firstScreen/cup.svg";
import avatars from "../../assets/images/firstScreen/avatars.webp";
import { Link } from "react-router-dom";
import { Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useSelector } from "react-redux";

const ReviewsCount = ({ t, buy }) => {
  const { mode } = useSelector(({ MainReducer }) => MainReducer);

  return (
    <Link to="/main/reviews">
      <Box
        display="flex"
        alignItems="center"
        flexWrap="nowrap"
        flexDirection={{ xs: "column", sm: "row" }}
        cursor="pointer"
      >
        <img src={avatars} alt="avatars" height={48} />
        <Box
          ml={3}
          display="flex"
          alignItems="center"
          flexWrap="nowrap"
          mt={{ xs: 1, sm: 0 }}
          mb={{ xs: 2, sm: 0 }}
        >
          <img src={buy && mode === "light" ? cup : cupWhite} alt="cup" />
          <Typography
            sx={{
              ml: 2,
              variant: "h6",
              color: buy ? "inherit" : "#FFF",
              textDecoration: "underline",
              whiteSpace: "nowrap",
            }}
          >
            {t("SatisfiedClients")}
          </Typography>
        </Box>
      </Box>
    </Link>
  );
};

export default ReviewsCount;
